<template>
    <v-container id="avatar-wrapper" fluid  no-gutters :style="{ height: height + 'px', width : width + 'px'}">
        <div id="divVHSS"></div>
        <v-row class="pa-3">
            <v-col cols="6">
                <h1>Listening: {{isListening}}</h1>
            </v-col>
            <v-col cols="6">
                <h1>Found User: {{looking}}</h1>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <div id="main" style="flex: 1" >
                    <video id="video" playsinline style="-webkit-transform: scaleX(-1);transform: scaleX(-1);width: 200px;height: 200px; background-color: black">
                    </video>
                </div>
            </v-col>
        </v-row>
        <v-spacer></v-spacer>

        <v-row align="end" style="height: 83.5%" class="avatar-accessories" no-gutters>



            <v-row>
                <v-col cols="2" sm="2"  >
                    <v-img src="../assets/homeDepot.png" class="logo-bubble ml-3 my-3" @click="speak('Welcome to Home Depot')"></v-img>
                </v-col>
                <v-col cols="10" sm="10">
                    <v-card v-if ="textBubble !== 'none'" :class="textBubble" class="ma-3 pa-2" id="text-bubble"  >
                        <p>{{bubbleText}}</p>
                    </v-card>
                </v-col>

            </v-row>


        </v-row>
    </v-container>
</template>


<script>

    export default {
        name: "Avatar",
        props: {
            initialWidth: {
                type: Number,
                required: true
            },
            textBubble: {
                //type: String,
                default: "bottom"
            }
        },
        data: function() {
            return {
                curLanguage: "en",
                isReady: false,
                isTalking: false,
                isListening: false,
                curSpeech: "",
                //width: this.initialWidth,
                width: screen.width,
                bubbleText: "",
                promise: null,
                tele: false,
                looking: false
            }
        },
        computed: {
            height: {
                get: function () {
                    return this.width * 1.778;
                },
                set: function (newVal) {
                    //this.width = newVal * 8/9;
                    this.width = newVal;
                }
            },
            spVoice: function() {
                switch (this.curLanguage) {
                    case 'es':
                        return 4;
                    default: // en
                        return 3;
                }
            },
            spLang: function() {
                switch (this.curLanguage) {
                    case 'es':
                        return 2;
                    default: // en
                        return 1;
                }
            },
            spEngine: function() {
                switch (this.curLanguage) {
                    case 'es':
                        return 4;
                    default: // en
                        return 3;
                }
            },
            textBubbleClass: function () {
                if (this.textBubble === 'bottom') {
                    return "bottom-bubble"
                }
                else if (this.textBubble === 'left') {
                    return ["side-bubble", "left-bubble"]
                }
                else if (this.textBubble === 'right') {
                    return ["side-bubble", "right-bubble"]
                }
                return ""
            },


        },
        watch: {
            width: function () {
                //window.dynamicResize(this.width * 1.5, this.height);
                window.dynamicResize(this.width, this.height);
                //this.tele = clientApp.tele;
            },
            curSpeech: function () {
                this.bubbleText = this.curSpeech;
            }
        },
        methods: {
            preloadSpeech: function(speechText) {
                window.loadText(speechText, this.spVoice, this.spLang, this.spEngine);
            },
            speak: function(speakText) {
                this.curSpeech = speakText;

                if (this.isTalking && this.promise) {
                    this.promise.resolve({interrupted : true});
                    this.promise = null;
                    window.stopSpeech();
                }

                const that = this;
                 //this.promise = new DeferredPromise();
                if (this.curLanguage === 'en') {
                    window.sayText('<prosody rate="+20%">' + speakText + '</prosody>', that.spVoice, that.spLang, that.spEngine);
                }
                else {
                    window.sayText(speakText, that.spVoice, that.spLang, that.spEngine);
                }

                return this.promise;
            },
            showResponse: function(response){
                this.bubbleText = response;
            },
            stopSpeaking: function() {
                window.stopSpeech();
            },
            triggerSpeechRecognition: function() {
                // rec.listen();
            },
            forceResize: function () {
                //window.dynamicResize(this.width * 1.5, this.height);
                window.dynamicResize(this.width, this.height);
            },
            endTele: function () {
                // tele.endTele();
            },
            closeOverlay: function () {
                window.overlayClose();
            },
            startOverlay: function () {
                window.overlayOpen('max',1);
            },
            telle: function () {
                this.speak("Hang on! I am trying to connect to the Court Clerk");
                this.tele = true;
                // tele.startTele();
            }

        },
        mounted: function () {
            //tele.startTele();

        }
    }
</script>


<style scoped>
    #divVHSS {
        width: 100%;
        z-index: 0;
    }

    .container{
        padding: 0 !important;
    }

    .logo-bubble {
        z-index: 1000000;
        min-height: 200px;
    }

    #text-bubble {
        background-color: rgba(30, 101, 39, 0.88);
        border-radius: 35px;
        color: white;
        text-align: center;
        border-style: solid;
        border-color: white;
        border-width: 2px;
        min-height: 200px;
        z-index: 1000000;
    }

    #text-bubble p {
        font-size: 3em;
    }

    .bottom-bubble {
        width: 90%;
        max-height: 50%;
        min-height: 15%;
        align-self: center;
        /*bottom: 0;*/
        /*left: 50%!important;*/
        /*transform: translateX(-50%)!important;*/
    }

    .side-bubble {
        display: table;
        width: 100%;
        max-height: 90%;
        min-height: 50%;
        position: absolute;
    }

    .side-bubble p {
        display: table-cell;
        vertical-align: middle;
    }

    .left-bubble {
        left: 0;
        top: 50%;
        transform: translate(-98%, -50%);
    }

    .right-bubble {
        right: -20%;
        top: 50%;
        transform: translate(98%, -50%);
    }

</style>